/** Api config options
 *
 * @param {String} sourceLanguageCode - ,     .    ISO 639-1 (, ru).       3.
 * @param {String} targetLanguageCode -  . ,    .    ISO 639-1 (, en).       3.
 * @param {String} format -  . PLAIN_TEXT:   .   . HTML:    HTML.
 * @param {Array of Strings} texts -  .    .       10000 .     .
 * @param {String} folderId -  ,      .
 */

function setData(config) {
  const data = config.data || {};
  config.data = Object.assign(data, {
    folderId: process.env.VUE_APP_FOLDER_ID,
    sourceLanguageCode: "en",
    targetLanguageCode: "ru",
  });

  return config;
}

function returnData(response) {
  //  data  interceptor    data
  return response.data;
}

export default function(axios) {
  axios.interceptors.request.use(setData);
  axios.interceptors.response.use(returnData);
}
